import axios from "../../config/axios";

const getTotalData = (tl) => {
  return { type: "GET_TOTAL_DATA", payload: tl };
};

const startGetTotalData = (date,proj) => {
  return (dispatch) => {

    const loadData = async () => {
      try {
        // console.log(date,"date")
        // console.log(`lead_stats?date=${date}&project_name=${proj}`,"proj")
        const TotalData=await axios.get(`lead_stats?date=${date}&project_name=${proj}`)
        // console.log(TotalData.data.data,"fse_list_redux")
        dispatch(getTotalData(TotalData.data.data))
        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};



export { getTotalData, startGetTotalData  };
