import "date-fns";
import React from "react";
import Grid from "@material-ui/core/Grid";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import { startDateData } from "../component/action/dateData";
import { connect } from "react-redux";
import moment from 'moment'
import "./MyForm.css"



function MaterialUIPickers(props) {
  const [selectedDate, setSelectedDate] = React.useState(
    new Date()
  );

  const handleDateChange = (date) => {
    setSelectedDate(date);
    let formatted_date = moment(date).format('DD/MM/YYYY');


    console.log(formatted_date, "date");
    const totalData = {
      date : formatted_date
    }
    const proj=props.projName
    props.dispatch(startDateData(totalData,proj));


  };

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <Grid container justifyContent="space-around">
        <KeyboardDatePicker
          disableToolbar
          variant="inline"
          format="dd/MM/yyyy"
          margin="normal"
          id="date-picker-inline"
          label="Date picker inline"
          value={selectedDate}
          onChange={handleDateChange}
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
        />
      </Grid>
    </MuiPickersUtilsProvider>
  );
}
const mapStateToProps = (state) => {
  return {
    projName: state.projName,
  };
};

export default connect(mapStateToProps)(MaterialUIPickers);
