import React,{useState} from "react";
// import { adaptV4Theme } from '@material-ui/core/styles';
import DataTable, { createTheme } from "react-data-table-component";
// import { table_data } from "./TableData";
import "./Table.css";
import {connect} from "react-redux"

import DataTableExtensions from "react-data-table-component-extensions";
createTheme("solarized", {
  text: {
    primary: "black",
    secondary: "#fff",
  },
  background: {
    default: "#fff",
  },
  boxshadow: {
    default: "5px 5px 30px #fff",
  },
  context: {
    background: "#fff",
    text: "#FFFFFF",
  },
  divider: {
    default: "#fff",
  },
  action: {
    button: "rgba(0,0,0,.54)",
    hover: "rgba(0,0,0,.08)",
    disabled: "rgba(0,0,0,.12)",
  },
});

function Table(props,{ api, admin }) {
  const [active,setActive] =useState(true)
  const columns = [
    {
      name: "Customer ID",
      selector: "customer_id",
      sortable: true,
      center: true,
      cell: (d) => <div>{d.customer_id}</div>,
    },
    {
      name: "Merchant Number",
      selector: "merchant_number",
      cell: (d) => <div>{d.merchant_number}</div>,
      sortable: true,
      center: true,
    },
    {
      name: "Store Name",
      selector: "shop_name",
      cell: (d) => <div>{d.shop_name}</div>,
      sortable: true,
      center: true,
    },
    {
      name: "Order Id",
      selector:"order_id",
      cell: (d) => <div>{d.order_id==="null"? "-":d.order_id}</div>,
      sortable: true,
      center: true,
    },
  ];
  const columns1 = [
    {
      name: "Customer ID",
      selector: "customer_id",
      cell: (d) => <div>{d.customer_id}</div>,
      sortable: true,
      center: true,
    },
    {
      name: "Merchant Number",
      
      selector: "merchant_number",
      cell: (d) => <div>{d.merchant_number}</div>,
      ordering: true,
      sortable: true,
      center: true,
      
    },
    {
      name: "Reason",
      selector: "reason",
      cell: (d) => <div>{d.reason}</div>,
      sortable: true,
      center: true,
    },
  ];
  
  

  // console.log(props.acceptedData,"data-total");

  const accepted={
    columns,
    data:props.acceptedData
    
  }
  const denied={
    columns:columns1,
    data:props.deniedData
  }

  const handleClick = () => {
    setActive(true)
    
  }
  const handleClick1 = () => {
    setActive(false)
   
  }
  
  return (
    <div className="table_data">
      <div className="menu-container">
          <h1 style={ active ? { color : "#4858e9"} : {color: "#979797" }} onClick={()=>handleClick()} >Accepted</h1>
          <h1 style={active ? { color : "#979797" , marginLeft: "130px"}: { color : "#ff968d", marginLeft: "130px"} } onClick={()=>handleClick1()}>Denied</h1>

          {/* <TripleDotDropDown admin={admin} /> */}
        </div>
        {active ? 
      <div className="card_table" id="card_table_accepted" >
        
        <DataTableExtensions
          {...accepted}
          // export={false}
          filterPlaceholder={`Search`}
        >
          <DataTable
            columns={columns}
            data={props.acceptedData}
            defaultSortFieldId={1}
            pagination

            theme="solarized"
          />
        </DataTableExtensions>
      </div>:
      <div className="card_table" id="card_table_denied" >
        <DataTableExtensions
          {...denied}
          // export={false}
          filterPlaceholder={`Search`}
        >
          <DataTable
            columns={columns1}
            data={props.deniedData}
            defaultSortFieldId={1}
            pagination
            theme="solarized"
          />
        </DataTableExtensions>
      </div>}
    </div>
  );
}

const mapStateToProps=(state)=>{
  return {
    totalData:state.totalData,
    acceptedData:state.totalData.accepted,
    deniedData:state.totalData.denied,
  }
}

export default connect(mapStateToProps)(Table)