import {startGetTotalData} from "./totalData"
const getDateData = (tl) => {
  return { type: "GET_DATE_DATA", payload: tl };
};

const startDateData = (data,proj) => {
  return (dispatch) => {

    const loadData = async () => {
      try {
        // console.log(proj,"inside date")
        // dispatch(getDateData(data))
        const date=data.date
        dispatch(startGetTotalData(date,proj))
        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};



export { getDateData, startDateData  };
